.ti_container{
    background-color: var(--tb-ti-desc-background);
    position: relative;
    display: table;
    width: 100%;
    bottom: auto;
    z-index: 9999;
    min-height: 25px;
    height: var(--tb-ticker-height);
    overflow: hidden;
}

.ti_container.ti_type-3{
    padding: 5px;
}

.tb_spotlight_ac .ti_container.ti_pos-2{
    position: fixed;
    bottom: 0;
    left: 0;
}

.ti_desc{
    font-size: var(--tb-ti-desc-font-size);
    font-family: var(--tb-ti-desc-font-family);
    color: var(--tb-ti-desc-color);
    white-space: nowrap;
}

.ti_marquee{
    width: 100%;
    height: 100%;
}

.ti_title_wrap{
    display: table-cell;
    vertical-align: middle;
    font-size: var(--tb-ti-title-font-size);
    font-family: var(--tb-ti-title-font-family);
    color: var(--tb-ti-title-color);
    background-color: var(--tb-ti-title-background);
}

.ti_m_wrap{
    display: table-cell;
    vertical-align: middle;
    width: 100%;
}

.ti_title{
    padding: 0 20px;
    text-align: center;
}

.ti_type-3 .ti_title, .ti_type-3 .ti_desc{
    line-height: calc(var(--tb-ticker-height) - 14px);
}

.ti_title, .ti_desc{
    line-height: calc(var(--tb-ticker-height) - 4px );
    white-space: nowrap;
}


.ti_type-3 .ti_title_wrap{
    border-radius: 3px;
}